import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, ActivatedRoute, Router } from '@angular/router';
import { Observable, empty } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { ProductService } from '../../../providers/product.services';
import { UserData } from '../../../providers/user-data';

@Injectable()
export class PrimaryDetailsResolver {
  constructor( private _route: Router, private _ProductService: ProductService,
    private _UserData: UserData) {}

  resolve(route: ActivatedRouteSnapshot) {
   return this.checkGoToHome();


  }


  getStoreList() {
    this._ProductService.fetchStoreList().subscribe((data: any) => {
      let storeList = data.storeList;
    alert("bhn")

      console.log(storeList,"list--")
      if (storeList.length == 1) {
        this._route.navigateByUrl("/home");
        localStorage.setItem("shopID", storeList[0]._id);
        return true
      }else{
        return true
      }
    });
  }

  checkGoToHome(){
    this.getCustomerProfile();
  }

  public userData:any;
  getCustomerProfile() {
    this._UserData
      .getCustomerDetails(localStorage.getItem("userId"))
      .subscribe((data: any) => {
        this.userData = data;
        console.log(this.userData,"this.userData");
        
        if(this.userData.city && this.userData.fullName ){
          this._route.navigateByUrl("/home");
        }
      });
  }


}


