import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { PrimaryDetailsPage } from './primaryDetails';
import { PrimaryDetailsResolver } from "./resolver/primaryDetails.resolver";

const routes: Routes = [
  {
    path: '',
    component: PrimaryDetailsPage,
    resolve: { users: PrimaryDetailsResolver }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class PrimaryDetailsPageRoutingModule { }
