import { Component, ViewChild, OnInit } from "@angular/core";
import { Router, ActivatedRoute } from "@angular/router";
import { ProductService } from "../../providers/product.services";
import { ModalController } from "@ionic/angular";
// import { ViewComplaintPage } from "./modal/viewComplaint";
import { ToastController } from "@ionic/angular";
import { UserData } from "../../providers/user-data";

@Component({
  selector: "page-primaryDetails",
  templateUrl: "primaryDetails.html",
  styleUrls: ["./primaryDetails.scss"],
})
export class PrimaryDetailsPage implements OnInit {
  public name: string = null;
  public city: string = null;
  public refercode:string = null;
  public isNameFieldEnable:Boolean


  public orderList: any;
  public storeList: any = [];
  public cartId: any;
  public des: any;
  public selectedOrder: any = "";
  public selectShop: any;

  public enableCheckoutScreen: boolean = false;
  constructor(
    private _ProductService: ProductService,
    public modalController: ModalController,
    private _Router: ActivatedRoute,
    public Router: Router,
    public toastController: ToastController,
    private _UserData: UserData
  ) {}

  public searchproductList: any = [];
  // public shop
  ngOnInit() {
    this.getStoreList();
    this.getCustomerProfile();

    // this.name = localStorage.getItem('userName')
    // console.log(this.name);

    // if(localStorage.getItem('userName')){
    //     this.isNameFieldEnable = true
    // }
    this.selectShop = localStorage.getItem('shopID') || ''
  }

  selectShopId() {
    console.log(this.selectShop, this.name);
  }

  getStoreList() {
    this._ProductService.fetchStoreList().subscribe((data: any) => {
      this.storeList = data.storeList;
      console.log(this.storeList);
    });
  }

  checkRegisteredDate() {

    if (this.userData) {
      if (
        new Date(this.userData.createdAt).setHours(0, 0, 0, 0) ==
        new Date().setHours(0, 0, 0, 0)
      ) {
        return true;
      } else {
        return false;
      }
    }
  }

  async goForward() {

    if (this.userData.fullName == null || this.userData.fullName == "") {
      if(!this.name){
        const toast = await this.toastController.create({
          message: "Please Enter Your Name",
           duration: 3000,
          color:'warning',
          position: "bottom",
          animated: true,
        });
        toast.present();
        return;
      }
    }


    if (this.userData.city == null || this.userData.city == "") {
      if(!this.city){
        const toast = await this.toastController.create({
          message: "Please Enter Your City",
           duration: 3000,
          color:'warning',
          position: "bottom",
          animated: true,
        });
        toast.present();
        return;
      }
    }


    // console.log(this.selectShop,"this.selectShop")
    // if (!this.selectShop) {

    //     const toast = await this.toastController.create({
    //       message: "Please Select Your Nearest Shop",
    //        duration: 3000,
    //       color:'warning',
    //       position: "bottom",
    //       animated: true,
    //     });
    //     toast.present();
    //     return;
    // }

    let self = this;
    let payload = Object.assign(
      {},
      {
        fullName: this.name,
        refer_code:this.refercode,
        ownerId: this._UserData.getOwnerId(),
        city:this.city
      }
    );

    let userId = localStorage.getItem("userId");
    console.log(payload, "payload" , userId);
    this._UserData
      .updateProfile(userId, payload)
      .subscribe(async (data: any) => {
        console.log("data", data);

        let selctedhopData =this.storeList.filter(function(val){
          return val._id == self.selectShop

        })
        console.log(selctedhopData[0].storeName,"selctedhopData")
        localStorage.setItem("userName",this.name)
         localStorage.setItem("shopID",this.selectShop);
         localStorage.setItem("shopName",selctedhopData[0].storeName)

        // const toast = await this.toastController.create({
        //   message: "You Successfully Changed you Profile",
        //    duration: 3000,
                // color:'secondary',,
        //   position: "bottom",
        //   animated: true,
        // });
        // toast.present();
          this.Router.navigateByUrl(`/home`);
      });
  }

  public selectedEntry: any = {
    _id: "",
  };
  onSelectionChange(entry) {
    this.selectedEntry = Object.assign({}, this.selectedEntry, entry);
    console.log(this.selectedEntry, "select");
    if (this.selectedEntry) {
      this.enableCheckoutScreen = true;
    }
  }

  //  import { Router } from '@angular/router';
  //     private router: Router,
  //   this.router.navigateByUrl('/tutorial');

  processedToCheckOutPage() {
    this.Router.navigateByUrl(
      `/checkout/${this.cartId}/${this.selectedEntry._id}`
    );
  }

  selectOrders() {
    console.log(this.selectedOrder);
  }

  public userData = {
    fullName: "",
    byReferal: "",
    city:"",
    createdAt: new Date(),
  };
  getCustomerProfile() {
    this._UserData
      .getCustomerDetails(localStorage.getItem("userId"))
      .subscribe((data: any) => {
        this.userData = data;
        console.log(this.userData,"this.userData");
        this.city = this.userData.city;
        this.name = this.userData.fullName;
        // this.name = userData.fullName;
        // console.log(this.name)
        // if(this.name){
        //     this.isNameFieldEnable = true
        // }
      });
  }

}
